// pages/cadastro-oficina.js
import React from 'react';
import { Button, TextField, Container, Typography } from '@mui/material';
import styled from 'styled-components';

const StyledContainer = styled(Container)`
  max-width: 600px;
  margin-top: 50px;
  padding: 20px;
  background-color: #f1efde; /* Cor de fundo */
  border-radius: 8px;
`;

const CadastroOficina = () => {
  return (
    <StyledContainer>
      <Typography variant="h4" color="#003366">Cadastro de Oficina</Typography>
      <form>
        <TextField label="Nome" fullWidth margin="normal" />
        <TextField label="CNPJ" fullWidth margin="normal" />
        <TextField label="CPF do Responsável" fullWidth margin="normal" />
        <TextField label="Localização" fullWidth margin="normal" />
        <TextField label="Serviços Oferecidos" fullWidth margin="normal" />
        <TextField label="Horário de Funcionamento" fullWidth margin="normal" />
        <TextField label="Formas de Pagamento" fullWidth margin="normal" />
        <Button variant="contained" color="primary" type="submit">Cadastrar</Button>
      </form>
    </StyledContainer>
  );
};

export default CadastroOficina;
